.header {
  background-color: var(--main-background-color);
}

.header .container {
  display: flex;
  align-items: center;
}

.header-icon {
  fill: var(--accent-color);
  margin-top: 12px;
  width: 40px;
  height: 37px;
  @media screen and (min-width: 768px) {
    margin-top: 20px;
    margin-left: 12px;
    width: 48px;
    height: 44px;
  }
  @media screen and (min-width: 1200px) {
    margin-top: 8px;
    margin-left: 0px;
    width: 60px;
    height: 65px;
  }
}

.header-nav {
  display: none;
  @media screen and (min-width: 768px) {
    display: none;
  }
  @media screen and (min-width: 1200px) {
    max-width: 1200px;
    display: block;
    margin-left: 270px;
    margin-top: 40px;
  }
}
.header-nav-list {
  display: flex;
  gap: 50px;
  cursor: pointer;
}

.header-nav-item {
  @include fonts(700, 32, 39, 0.03);
  color: var(--secondary-text-color);
  @media screen and (min-width: 768px) {
    @include fonts(700, 48, 58, 0.03);
  }
  @media screen and (min-width: 1200px) {
    @include fonts(700, 16, 20, 0.03);
    cursor: pointer;
  }
}

.header-nav-item:hover {
  color: var(--accent-color);
}

.header-button {
  font-family: 'Roboto';
  font-style: normal;
  @include fonts(600, 14, 16, 0.03);
  color: var(--primary-text-color);
  background-color: var(--accent-color);
  border-color: var(--accent-color);
  border-radius: 10px;
  width: 152px;
  height: 42px;
  display: flex;
  align-items: center;
  justify-content: center;
  display: none;
  @media screen and (min-width: 768px) {
    display: block;
    position: absolute;
    top: 24px;
    right: 32px;
  }
  @media screen and (min-width: 1200px) {
    display: block;
    position: absolute;
    top: 28px;
    right: 16px;
  }
}

.mob-menu-btn {
  position: absolute;
  top: 16px;
  right: 20px;
  @media screen and (min-width: 768px) {
    position: absolute;
    top: 28px;
    right: 216px;
  }
  @media screen and (min-width: 1200px) {
    display: none;
  }
}
.mob-menu-button {
  background-color: var(--main-background-color);
  border: none;
}

.mob-menu-icon {
  justify-content: center;
  align-content: center;
  fill: var(--secondary-text-color);

  @media screen and (min-width: 768px) {
  }
  @media screen and (min-width: 1200px) {
  }
}

